<template>
  <div class="ass_host mainboss">
    <div class="martop">
      <div class="topleft">
        <el-input
          v-model="roleName"
          placeholder=""
          @keyup.enter.native="getrole(1)"
          clearable
        ></el-input>
        <el-button type="primary" @click="getrole(1)"></el-button>
      </div>
      <div class="topright">
        <el-button type="primary" icon="el-icon-plus" @click="addhost"></el-button>
        <el-button type="primary" icon="el-icon-plus" @click="addbatch"></el-button>
        <el-button type="primary" icon="el-icon-delete" @click="batchdel"></el-button>
      </div>
    </div>
    <el-table
      :data="userlist"
      :height="this.$store.state.tableHeight"
      resizable
      border
      style="width: 100%"
      v-loading="loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      element-loading-text=""
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column prop="username" align="center" sortable label=""></el-table-column>
      <el-table-column prop="trueName" align="center" sortable label="IP"></el-table-column>
      <el-table-column prop="username" align="center" sortable label="IP"></el-table-column>
      <el-table-column prop="trueName" align="center" sortable label="IDC"></el-table-column>
      <el-table-column prop="username" align="center" sortable label=""></el-table-column>
      <el-table-column prop="trueName" align="center" sortable label=""></el-table-column>
      <el-table-column prop="username" align="center" sortable label=""></el-table-column>
      <el-table-column fixed="right" label="" align="center">
        <template slot-scope="scope">
          <el-button
            :disabled="!scope.row.status"
            @click="handleedit(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
          <el-button
            :disabled="!scope.row.status"
            @click="handledel(scope.row)"
            type="primary"
            size="mini"
            icon="el-icon-edit"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <Pagein
      :total="total"
      @handleCurrentChange="handleCurrentChange"
      @handleSizeChange="handleSizeChange"
    ></Pagein>

    <el-dialog
      title=""
      :visible.sync="AddDialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="30%"
      min-width="30%"
      center
    >
      <p class="prompt"></p>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="host">
          <el-input v-model="ruleForm.host" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="IP" prop="IP">
          <el-input v-model="ruleForm.IP" clearable placeholder="IP"></el-input>
        </el-form-item>
        <el-form-item label="IP">
          <el-input v-model="ruleForm.publicIp" clearable placeholder="IP"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="ruleForm.port" clearable></el-input>
        </el-form-item>
        <el-form-item label="IDC">
          <el-select v-model="ruleForm.IDC" placeholder="IDC">
            <el-option v-for="item in IDCs" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="ruleForm.region" clearable placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="management">
          <el-select v-model="ruleForm.management" placeholder="">
            <el-option
              v-for="item in managements"
              :key="item.id"
              :label="item.label"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="ruleForm.label" placeholder="">
            <el-option v-for="item in labels" :key="item.id" :label="item.label" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc" maxlength="255" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title=""
      :visible.sync="batchDialogVisible"
      :modal-append-to-body="false"
      top="3%"
      width="30%"
      min-width="30%"
      center
    >
      <p class="prompt">: [hostname] [ip] [port] [] , </p>
      <el-form ref="form" :model="form" label-width="0px">
        <el-form-item label>
          <el-input
            type="textarea"
            :autosize="{ minRows: 14, maxRows: 30}"
            placeholder="ops-hostname 10.0.0.1 22 ec2-user"
            v-model="form.textarea2"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')"></el-button>
          <el-button @click="resetForm('form')"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import Pagein from "../../public/pagein";
export default {
  name: "ass_host",
  data() {
    return {
      roleName: "",
      total: 0,
      loading: false,
      AddDialogVisible: false,
      batchDialogVisible: false,
      form: {
        textarea2: ""
      },
      ruleForm: {
        host: "",
        IP: "",
        publicIp: "",
        port: "",
        IDC: "",
        region: "",
        management: "",
        label: "",
        desc: ""
      },
      rules: {
        host: [{ required: true, message: "", trigger: "blur" }],
        IP: [{ required: true, message: "IP", trigger: "blur" }],
        management: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      IDCs: [
        {
          id: "001",
          label: "IDC1"
        },
        {
          id: "002",
          label: "IDC2"
        },
        {
          id: "003",
          label: "IDC3"
        }
      ],
      managements: [
        {
          id: "001",
          label: "1"
        },
        {
          id: "002",
          label: "2"
        },
        {
          id: "003",
          label: "3"
        }
      ],
      labels: [
        {
          id: "001",
          label: "label1"
        },
        {
          id: "002",
          label: "label2"
        },
        {
          id: "003",
          label: "label3"
        }
      ],
      userlist: [],
      dellist: []
    };
  },
  components: {
    Pagein
  },
  methods: {
    //
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    addhost() {
      this.AddDialogVisible = true;
    },
    //
    addbatch() {
      this.batchDialogVisible = true;
    },
    //
    batchdel() {
      if (this.dellist.length) {
      } else {
        this.$message.error("");
      }
    },
    //
    handleedit(obj) {
      this.AddDialogVisible = true;
    },
    //
    handledel(obj) {
      console.log("handledel");
    },
    //
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(formName);
          if ((formName == "form" && !this.form.textarea2)) {
            this.$message.error("");
          } else {
            this.$message.success("");
            this.AddDialogVisible = false;
            this.batchDialogVisible = false;
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //
    handleCurrentChange(val) {
      console.log("handleCurrentChange", val);
    },
    //
    handleSizeChange(val) {
      console.log("handleSizeChange", val);
    }
  }
};
</script>





<style lang="less" scoped>
.topleft {
  .el-input {
    width: 70%;
  }
}
.prompt {
  padding: 10px 0;
  text-align: center;
}
</style>


